import Vue from "vue";
import store from "../store/sportIndexStore";
import sportMatchDetail from "../components/sportMatchDetail.vue";
import {post,fetch,patch,put,delet} from '../utils/axios';
import vuescroll from 'vuescroll';
import 'vuescroll/dist/vuescroll.css';
import promise from 'es6-promise';
promise.polyfill();
Vue.use(vuescroll);

//定义全局变量
Vue.prototype.$post = post;
Vue.prototype.$fetch = fetch;
Vue.prototype.$patch = patch;
Vue.prototype.$put = put;
Vue.prototype.$delet = delet;

// import "../utils/component.js"
var app = new Vue({
    el: '#app',
    store,
    render: h => h(sportMatchDetail)
})