<template>
    <div :class="{nav: true, miguNavWhite: isShow == 1, miguNavBlack: isShow == 2}">
        <div class="nav-container clearfix">
            <!-- <div class="migu-logo" :class="{miguLogoWhite: isShow == 2, miguLogoBlack: isShow == 1}" @click="jumpIndex"></div> -->
            <ul class="clearfix" v-if="navDataLi">
                <li v-for="(item, index) in navDataLi" :key="index" @click="actNav(index, item)">
                    <div v-text="item.title" :class="{active: actIndex == index}"></div>
                </li>
            </ul>
        </div> 
    </div>
</template>
<script>
import {mapState, mapActions, mapGetters} from 'vuex';
import Bus from '../../../utils/bus';
import webCommon from "common/webcommon";
import {commonHandle} from "common/actionHandle";
import utils from 'utils/utils.js';
export default {
    name: 'sNav',
    props: ['isShow', 'mark'], // mark 标记在首页 true , 赛程页 false
    data() {
        return {
            actIndex: 0,
            navLength: null,
            channelPageId: 'd263e640cd9846bfbdc4ffd7a4e87d90', // 首页的 pageId
            lists: []
        }    
    },
    mounted() {
        // this.actIndex = getQueryString('index') || this.navLength - 1;
        this.navData({id: 'f6d46f0babec4b6984e212089c113a84'});
    },
    watch: {
        navList(newValue, oldValue) {
            var index = sessionStorage.getItem("index");
            this.lists = newValue[0].components[0].extraData.menus;
            let urlPageId = utils.getQueryString('pageId');
            if(urlPageId) {
                for (let i = 0; i < this.lists.length; i ++) {
                    if (urlPageId == this.lists[i].action.params.path) {
                        index = this.lists.length - (i + 1);
                        sessionStorage.setItem('index',index);
                        sessionStorage.setItem('competitionId',this.lists[i].competitionId);
                    }
                }
            }
            if (!newValue || !newValue.length || !newValue[0].components || !newValue[0].components.length || !newValue[0].components[0].extraData.menus || !newValue[0].components[0].extraData.menus.length) return;
            this.navLength = newValue[0].components[0].extraData.menus.length;
            this.actIndex = index || 0; 
            if (this.mark == 1) { 
                if ( index == 1 || index == 2) {
                    this.actIndex = 0;
                    this.$emit('getPageId', this.channelPageId); // 定位到首页并展示首页的数据
                }
            } else if (this.mark == 2){
                this.actIndex = 2; // 定位到赛程
            } else {
                this.actIndex = 1;
            }
        }
    },
    computed: {
        ...mapState('ppsport', {
            navList: state => state.navLists,
            competitionID: state => state.competitionID
        }),
        navDataLi() {
            let nav = null;
            try {
                this.lists = nav = this.navList[0].components[0].extraData.menus.reverse();
            } catch(err) {

            }
            return nav;
        }
    },
    methods: {
        ...mapActions('ppsport', [
            'navData'
        ]),
        actNav(index, item) {
            this.actIndex = index;
            sessionStorage.setItem("index", index); // 本地储存导航栏的索引
            item.competitionId ? item.competitionId : 'undefined';
            sessionStorage.setItem('competitionId', item.competitionId); // 本地储存赛事id
            Bus.$emit('valId', item.competitionId); // 传递赛事id给二级轮播图
            if (this.mark == 1) { // 首页
                switch(item.title) {
                    case '赛事数据':
                        sessionStorage.setItem('pageId', this.channelPageId); // 默认返回到首页
                        commonHandle(webCommon.SPORT_DATA_PAGE_ID);
                        break;
                    case '赛程':
                        commonHandle(webCommon.SPORT_DEATIL_PAGE_ID);
                        break;
                    default: 
                    this.$emit('getPageId', item.action.params.path); // 传递 pageId 给首页
                }
            } else if (this.mark == 2){ // 赛程中心页
                switch(item.title) {
                    case '赛程': 
                        commonHandle(webCommon.SPORT_DEATIL_PAGE_ID);
                        break;
                    case '赛事数据': 
                        commonHandle(webCommon.SPORT_DATA_PAGE_ID);
                        break;
                    default: 
                    sessionStorage.setItem('pageId', item.action.params.path);
                    commonHandle(webCommon.SPORT_INDEX_PAGE_ID);
                }
            } else {
                switch(item.title) {
                    case '赛程':
                        commonHandle(webCommon.SPORT_DEATIL_PAGE_ID);
                        break;
                    case '赛事数据':
                        commonHandle(webCommon.SPORT_DATA_PAGE_ID);
                        break;
                    default:
                        sessionStorage.setItem('pageId', item.action.params.path);
                        commonHandle(webCommon.SPORT_INDEX_PAGE_ID);
                }
            }
        },
        jumpIndex() {
            commonHandle(webCommon.INDEX_PAGE_ID);
        }
    }
}
</script>

<style lang='less' scoped>
    .miguLogoWhite {
        background-image: url('../../../../assets/sports/migu.png');
        background-size: 100% 100%;
    }
    .miguLogoBlack {
        background-image: url('../../../../assets/sports/migu_logo.png');
        background-size: 100% 100%;
    }
    .miguNavWhite {
        background: #FFF;
        color: #666;
    }
    .miguNavBlack {
        background: rgba(0,0,0,.5);
        color: #fff;
    }
    @media screen and (max-width: 1640px){
        .nav {
            width: 100%;
            height: 60px;
            box-shadow: 0 2px 8px 0 rgba(0,0,0,.1);
            .nav-container {
                width: 1160px;
                height: 60px;
                margin: 0 auto;
                // .migu-logo {
                //     float: left;
                //     width: 200px;
                //     height: 60px;
                //     cursor: pointer;
                // }
                ul {
                    li {
                        float: left;
                        padding: 0 10px;
                        font-size: 14px;
                        cursor: pointer;
                        div {
                            position: relative;
                            height: 50px;
                            line-height: 60px;
                        }
                        div.active {
                            font-weight: bold;
                            color: #007AFF;
                            font-weight: bold;
                            &::after {
                                content: "";
                                position: absolute;
                                bottom: 0;
                                left: 0;
                                width: 100%;
                                height: 4px;
                                background: #007AFF;
                                border-radius: 2px;
                            }
                        }
                    }
                    li:nth-child(1) {
                        padding-left: 0;
                    }
                }
            }
        }
    }
    @media screen and (min-width: 1640px){
        .nav {
            width: 100%;
            height: 60px;
            box-shadow: 0 2px 8px 0 rgba(0,0,0,.1);
            .nav-container {
                width: 1640px;
                height: 60px;
                margin: 0 auto;
                // .migu-logo {
                //     float: left;
                //     width: 200px;
                //     height: 60px;
                //     cursor: pointer;
                // }
                ul {
                    li {
                        float: left;
                        padding: 0 10px;
                        font-size: 14px;
                        cursor: pointer;
                        div {
                            position: relative;
                            height: 50px;
                            line-height: 60px;
                        }
                        div.active {
                            font-weight: bold;
                            color: #007AFF;
                            font-weight: bold;
                            &::after {
                                content: "";
                                position: absolute;
                                bottom: 0;
                                left: 0;
                                width: 100%;
                                height: 4px;
                                background: #007AFF;
                                border-radius: 2px;
                            }
                        }
                    }
                    li:nth-child(1) {
                        padding-left: 0;
                    }
                }
            }
        }
    }
</style>
