import webCommon from 'common/webCommon';
import { fetch } from 'utils/axios';
import  API  from '@@/common/api.js';
const state = {
  action : String
}

const getters = {}

const mutations = {
  // 获取下载链接的action
  getDownloadAction(state,data) {
    state.action = data
  }
}

const actions = {
  // 请求下载链接，获取下载链接的action
  async getDownloadLink({commit},param = webCommon.DOWNLOAD_PAGE_ID) {
    const res = await fetch(API.PAGE_LAYOUT_PATH + param);
    // console.log(res,'ppppppppp')
    if(res.code == 200) {
      const {body:{groups:[{components:[{extraData:{labels:[{action}]}}]}]}} = res
      // console.log(action,'action4444444')
      commit('getDownloadAction',action)
    }
  }
}

export default {
  namespaced: true,
  state,
  actions,
  getters,
  mutations 
}