import API from '../../../common/api';
import {fetch, post} from '../../../utils/axios';
import { dataGroupFilter, getGroupRequestList, mergeComp } from '../../model/indexModel';
import LoadManager from 'utils/loadManager';
const state = {
    eventList: [{name: '全部', competitionId: 'all'}, {name: '西甲', competitionId: 4}, {name: '英超', competitionId: 5}, 
    {name: '德甲', competitionId: 6}, {name: '意甲', competitionId: 7}, {name: '欧冠', competitionId: 8}, {name: '法甲',competitionId: 9}, {name: '欧联', competitionId: 11}, {name: 'CBA', competitionId: 2221401}],
    teamLogo: [
        require('@/sports/xijia101.png'), require('@/sports/yingchao101.png'), require('@/sports/dejia101.png'),
        require('@/sports/yijia101.png'), require('@/sports/ouguan101.png'), require('@/sports/fajia101.png'),
        require('@/sports/ounian101.png'), require('@/sports/cba101.png'),
    ],
    nameList: [],
    dayData: [],
    groups: null, 
    navLists: null,
    competitionID: null,
    mgdbId: '',
    showLoginIfr: false
};
const mutations = {
    set_key_value(state, { key, value }) {
        state[key] = value;
    },
    nameLists(state, data) {
        state.eventList = data;
        let arr = data.map(item => item.competitionName);
        state.nameList = [...new Set(arr)]; // 数组去重
    },
    dataList(state, data) {
        state.dayData = data; // 直播数据展示的信息
    },
    setNavGroups(state, groups) {
        state.navLists = groups;
    },
    setGroups(state, groups) {
        state.groups = groups;
    },
    setCompetitionId(state, data) {
        state.competitionID = data.id;
    },
    setGroupList(state, items) {
        for (let item of items) {
            let index = item.index;
            if (index < 0 || index >= state.groups.length) return;
            let group = state.groups[item.index];
            let comps = group.components;
            let rCopms = null;
            try {
                if (item.data && item.data.code == 200) {
                    rCopms = item.data.body.components;
                }
            } catch (e) {}

            if (group) {
                mergeComp(group, rCopms, true);
            }
        }
    },
    setMgdbid(state,data){
        state.mgdbId = data
    }
};
const getters = {
    clickWhitch : (state,commit) =>{
        let res = state.dayData.filter((item)=>{
            if(item.mgdbId === state.mgdbId){
                commit('setAAAA', item)
                return item
            }
        })
        return res[0]
    }
};
function getList(date, competitionID, type) {
    return post(API.GET_LIVE_LISTS.replace(/date/, date).replace(/competitionID/, competitionID).replace(/type/, type));
};
const actions = {
    async eventInterface({commit}, {date, competitionID, type}) {
        if (competitionID == 'undefined' || !competitionID) return;
        let todayList = await getList(date[0], competitionID, type);
        const {body: {keys, matchList}} = todayList;
        let todayMatchList = [];
        for (let item in matchList) {
            for (let i = 0; i < date.length; i ++) {
                if (item == date[i]) {
                    todayMatchList.push(...matchList[item]);
                }
            }
        }
        commit('dataList', todayMatchList);
    },
    demandData({commit}, {id}) {
        fetch(API.PAGE_LAYOUT_PATH + `${id}`).then(function(presentData) {
            const {body: {groups, name}} = presentData;
            let competitionId = null;
            if (!groups || !groups.length || !groups[1].components || !groups[1].components.length || !groups[1].components[0].extraData || !groups[1].components[0].extraData.competitionId) {
                competitionId = '';
            } else {
                competitionId = groups[1].components[0].extraData.competitionId;
            }
            commit('setCompetitionId', {id: competitionId});
            let filtergroups = dataGroupFilter(groups,true);
            commit('setGroups', filtergroups);
            let requestgroups = getGroupRequestList(filtergroups);
            new LoadManager(requestgroups, function(items) {
                if (items) commit('setGroupList', items);
            });
        });
    },
    navData({commit}, {id}) {
        fetch(API.PAGE_LAYOUT_PATH + `${id}`).then(function(presentData) {
            const {body: {groups, name}} = presentData;
            let competitionId = ['', '', '', '', '7', '6', '9', '4', '8', '1', '', '5', '', '2221401', '', '', 'all'] // 赛事的 id 
            // console.log(groups[0].components[0].extraData.menus);
            if (!groups || !groups.length || !groups[0].components.length || !groups[0].components[0].extraData.menus.length) return;
            let lists = groups[0].components[0].extraData.menus.reverse();
            for (let i = 0; i < lists.length; i ++) {
                lists[i].competitionId = competitionId[i]; // 向每个赛事添加对应的赛事id
            }
            commit('setNavGroups', groups);
        });
    }
}
export default {
    namespaced: true, // 区分不同模块
    state,
    mutations,
    getters,
    actions
}