<template>
  <div class="liveEvents" @click="channelHide">
    <w-header @showLogin="goLogin()" :userData = "userdata" :headerNavList = "headerNavList" :markNav = "{flag, isChange}"></w-header>
    <div class="snav">
        <s-nav :isShow="isShow" :mark="mark"></s-nav>
    </div>
    <div class="liveEventsContent">
        <div class="eventList fl">
            <div class="title">赛程直播表</div>
            <div class="subList">
                <div class="subTitle">赛事列表</div>
                <ul>
                    <li v-for="(item, index) in eventList" :key="index"  @click="changeEvent(item.competitionId)" :class="{subActive: item.competitionId == whichEvent}">
                        <div class="leftNav" :style="{backgroundImage: 'url('+ teamLogo[index-1] +')'}"></div>
                        <p v-html="item.name" ></p>
                    </li>
                </ul>
            </div>
        </div>
        <div class="eventContent fr">
            <div class="dateList">
                <div class="swiper-container date-list">
                    <div class="swiper-wrapper">
                        <div class="swiper-slide" v-for="(item,index) in dateList" :key="index" :class="{dateActive: index == num}" >
                            <div class="slideContent">
                                <div class="date" v-html="item.date">10月05日</div>
                                <div class="week" v-html="item.wk">星期五</div>
                            </div>
                        </div>
                    </div>
                    <!-- 如果需要导航按钮 -->
                    <div class="swiper-button-prev"></div>
                    <div class="swiper-button-next"></div>
                </div>
            </div>
            <div class="content">
                <vue-scroll :ops="ops">
                    <ul v-if="dataList.length">
                        <li v-for="(item, index) in dataList" :key="index">
                            <div class="eventTime" v-text="item.keyword.substring(6)"></div>
                            <div class="eventIten">
                                <div class="eventName">
                                    <div class="team-name left" v-text="item.confrontTeams[0].name"></div>
                                    <div class="team-logo left">
                                        <img :src="item.confrontTeams[0].image" alt="">
                                    </div>
                                    <div class="team-score" v-text="item.confrontTeams[0].score*1>0?item.confrontTeams[0].score:0"></div>
                                </div>
                                <div class="eventDes">
                                    <div v-html="item.title"></div>
                                    <div class="status" v-if="compareCurrentTime(item.startTime,item.endTime) == 1">结束</div>
                                    <div class="status" v-if="compareCurrentTime(item.startTime,item.endTime) == 2">进行中</div>
                                    <div class="status" v-if="compareCurrentTime(item.startTime,item.endTime) == 3">未开始</div>
                                </div>
                                <div class="eventName">
                                    <div class="team-score" v-text="item.confrontTeams[1].score*1>0?item.confrontTeams[1].score:0"></div>
                                    <div class="team-logo right">
                                        <img :src="item.confrontTeams[1].image" alt="">
                                    </div>
                                    <div class="team-name right" v-text="item.confrontTeams[1].name"></div>
                                </div>
                            </div>
                            <div class="eventRight" @click="detailLivePage(item)" :class="{end: compareCurrentTime(item.startTime,item.endTime) == 1,living: compareCurrentTime(item.startTime,item.endTime) == 2, appointment: compareCurrentTime(item.startTime,item.endTime) == 3}"></div>
                        </li>
                    </ul>  
                    <div class="empty" v-else>今日无比赛！</div>  
                </vue-scroll> 
            </div>
        </div>
        <!-- // 日历icon -->
        <div class="calendar" @click="backToday"></div>
    </div>
    <div v-if="isDisplay">
        <down-load-app :isDisplay="isDisplay" @close="close"></down-load-app>
    </div>
    <s-footer></s-footer>
    <FloatBtn @openFeedback="openFeedback()"></FloatBtn>
    <login-iframe v-if="showLoginIfr" @closeLogin="closeLoginIfr"></login-iframe>
  </div>
</template>

<script>
import wHeader from 'components/group/header';
import sNav from './group/sports/sport_slid_nav';
import Swiper from 'Swiper';
import {mapState, mapActions, mapGetters} from 'vuex';
import {API} from '../common/api.js';
import H5CommonUtils from '../utils/utils.js';
import actionHandle from '../common/actionHandle';
import webCommon from "common/webcommon";
import {commonHandle} from "../common/actionHandle";
import {delet, fetch, post} from '../utils/axios';
import downLoadApp from './group/sports/downLoadApp';
import utils from 'utils/utils.js'
import sFooter from './group/footer';
import FloatBtn from "./lib/floatBtn";
import loginIframe from 'components/group/loginIframe';
import KSStatistics from "KSStatistics"
export default {
    name: '',
    data () {
        return {
            ops: { // 滚动条初始化
                bar: {
                    keepShow: true,
                    background: 'rgb(204,204,204)',
                    opacity: 1,
                    hoverStyle: false,
                    keepShow: false,
                    specifyBorderRadius: false,
                }
            },
            isShow: 1, // 改变导航栏样式
            mark: 2, // 标记当前导航在哪个页面
            whichEvent: 'all', // 比赛的 competitionId
            date: H5CommonUtils.getFormatDate(1), // 当天日期 20181025
            dateList: [], // 当前日期的前一周与后一周
            num: 7, // 默认当前时间居中显示
            mySwiper: {},
            isDisplay: false,
            flag: null,
            isChange: null,
            isClient: null
        }
    },
    components: {
        sNav,
        downLoadApp,
        wHeader,
        sFooter,
        FloatBtn,
        loginIframe
    },
    computed: {
        ...mapState('ppsport', {
            eventList: state => state.eventList,
            dataList: state => state.dayData,
            teamLogo: state => state.teamLogo,
            showLoginIfr: state => state.showLoginIfr
        }),
        ...mapState(['userdata'])
    },
    created () {
        document.title = `咪咕视频-体育赛程, 让生活更有戏`;
        this.dateList = this.dateArrObj();
        this.isClient = (window.external && window.external.Play) || (window.migu_app && window.migu_app.Play);
        // 埋点
        KSStatistics.setStaticParam({
            pageName : "体育赛程页",
        }).ready().PV();
    },
    mounted() {
        this.getDownloadLink();
        this.initSwiper();
        window.addEventListener('scroll', this.boxScrollTop);
            window.addEventListener('message',e=>{
            // console.log("message");
            if(e && e.data && (typeof e.data) === 'string'){
                let d1 = JSON.parse(e.data);
                if(d1.type == "login"){
                    this.closeLoginIfr();
                    this.$store.commit('userdata/updateUserInfo');
                    this.$store.dispatch('history/getPlayHistory', {
                        start: 0,
                        limit: 100,
                        all: 1,
                        prdInfoIds: 1002601
                    });
                }
            }
        });
    },
    filters: {
        // <!-- {{item.startTime | formatTime}} -->
        formatTime(timestamp){
            return H5CommonUtils.timeFormat(timestamp, true, false).substring(0, 5).replace(/(.{3})/g,'$1 ');
        }
    },
    methods: {
        close(value) {
            // console.log(value);
            this.isDisplay = value;
        },
        ...mapActions('ppsport', [
            'eventInterface'
        ]),
        changeEvent(competitionId){
            this.whichEvent = competitionId;
            this.eventInterface({date:[this.date], competitionID: this.whichEvent, type:'default'});
        },
        // 初始化swiper 
        initSwiper(){
            var that = this
            this.mySwiper = new Swiper ('.date-list', {
                spaceBetween: 15,
                touchRatio: 0,
                slidesPerView : 'auto',
                freeMode: true,
                centeredSlides : true,
                slideToClickedSlide: true,
                initialSlide: that.num,
                observer: true,
                observerParent: true,
                // 如果需要前进后退按钮
                navigation: {
                    nextEl: '.swiper-button-next',
                    prevEl: '.swiper-button-prev',
                },
                on: {
                    slideChangeTransitionStart: function(){
                        that.num = this.activeIndex;
                        that.date = that.dateList && that.dateList[this.activeIndex].num;
                        that.eventInterface({date:[that.date], competitionID: that.whichEvent, type:'default'});
                    }
                }, 
            })
        },
        // 点击calendar图标
        backToday(){
            let date = H5CommonUtils.getFormatDate(1);
            this.eventInterface({date: [date], competitionID: this.whichEvent, type: 'default'});
            let realWidth = this.mySwiper.slidesGrid[7];
            this.num = this.mySwiper.activeIndex = 7;
            this.mySwiper.setTransition(300);//设定过渡的时间
            this.mySwiper.setTranslate(-realWidth);
        },
        // 生成日期数组对象
        dateArrObj(){
            let resultArr = [];
            for(let i = -7; i < 8; i ++){
                resultArr.push(H5CommonUtils.GetDateStr(i));
            }
            return resultArr;
        },
        // 与当前时间比较
        compareCurrentTime(startTimestamp, endTimestamp){
            let currentTimestamp = new Date().getTime();
            if (currentTimestamp > endTimestamp) {
                return 1; // 直播结束
            } else if (startTimestamp > currentTimestamp){
                return 3; // 未开始
            } else {
                return 2; // 直播中
            }
        },
        detailLivePage(item) {
            let currentTimestamp = new Date().getTime();
            if (item.startTime > currentTimestamp) { // 未开始
                this.isDisplay = true;
                return; 
            } else if (item.endTime < currentTimestamp) { // 回看
                item.liveType = '';
            } else {
                item.liveType = webCommon.SPORT_LIVE_DETAIL;
            }
            if (this.isClient) {
                fetch(API.SPORTS_PID + item.mgdbId).then(data => {
                    let params = {cid: data.body && data.body.pID, type: item.liveType, mgdbId: item.mgdbId};
                    commonHandle(webCommon.SPORT_LIVE_DETAIL, params);
                });
            } else {
                commonHandle(webCommon.SPORT_LIVE_DETAIL, {cid: '', type: item.liveType, mgdbId: item.mgdbId});
            }
            // fetch(API.SPORTS_PID + item.mgdbId).then(data => {
            //     let params = {cid: data.body && data.body.pID, type: item.liveType};
            //     commonHandle(webCommon.SPORT_LIVE_DETAIL, params); 
            // });
        },
        channelHide() {
            this.flag = true;
            this.isChange = !this.isChange;
            utils.clickHide();
        },
         // 登录相关
        goLogin(){
            this.$store.commit('ppsport/set_key_value',{key:'showLoginIfr',value:true})
        },
        closeLoginIfr(){
            this.$store.commit('ppsport/set_key_value',{key:'showLoginIfr',value:false});
        },
        ...mapActions('downloadLink',['getDownloadLink']),
    }
}
</script>
<style lang='less'>
@import "~lib/swiper/3.4.2/swiper.css";
@import "../../css/group/header_other.less";
.subActive{
    background-color: #0092DC ;
    color: #fff;
}
.dateActive{
    background-color: #0092DC ;
    color: #fff!important;
    width: 181px;
    height: 85px;
    &::after{
        display: block;
        content: "";
        position: absolute;
        border: 10px solid transparent;
        border-top-color: #0092DC ;
        bottom: -20px;
        left: 44.5px;
    }
}
.live-ev-content(@width, @mar-top) {
    height: 100%;
    width: @width;
    margin: 0 auto;
    margin-top: @mar-top;
    position: relative;
}
.live-ev-list(@width ,@height) {
    width: @width;
    height: @height;
    border: 1px solid #CBCBCB;
}
.live-ev-list-title(@height, @font-size, @letter-spacing) {
    height: @height;
    line-height: @height;
    text-align: center;
    border-bottom: 1px solid #CBCBCB;
    font-size: @font-size;
    color: #3B3B3B;
    letter-spacing: @letter-spacing;
    font-weight: bold;
}
.live-ev-list-sub-list(@font-size, @letter-spacing) {
    text-align: center;
    font-size: @font-size;
    color: #3B3B3B;
    letter-spacing: @letter-spacing;
}
.live-ev-list-sub-title(@height, @pad-left, @font-size, @letter-spacing) {
    height: @height;
    line-height: @height;
    border-bottom: 1px solid #f9f9f9;
    padding-left: @pad-left;
    font-size: @font-size;
    color: #474747;
    letter-spacing: @letter-spacing;
    text-align: left;
}
.live-ev-list-ul-li(@height, @pad-left) {
    height: @height;
    line-height: @height;
    padding-left: @pad-left;
    box-sizing: border-box;
    cursor: pointer;
}
.live-ev-list-ul-li-left-nav(@width, @top, @right, @bottom, @left) {
    width: @width;
    height: @width;
    background-size: 100% 100%;
    float: left;
    margin: @top @right @bottom @left;
}
.live-ev-list-ul-li-first(@pad-left) {
    padding-left: @pad-left;
}
.live-ev-container(@width) {
    width: @width;
}
.live-ev-container-date-list(@width, @height) {
    width: @width;
    overflow: hidden;
    height: @height;
}
.live-date-list {
    background-color: #F2F2F2;
    overflow: visible;
}
.live-button-prev-next(@width, @height, @mar-top) {
    width: @width;
    height: @height;
    margin-top: @mar-top;
}
.live-swiper-slide(@width, @height, @font-size, @letter-spacing) {
    width: @width!important;
    height: @height;
    font-size: @font-size;
    color: #7E7E7E;
    letter-spacing: @letter-spacing;
    display: table;
    vertical-align: middle;
    cursor: pointer;
}
.live-ev-container-content(@width, @height, @mar-top) {
    width: @width;
    height: @height;
    border: 1px solid #CBCBCB;
    margin-top: @mar-top;
}
.live-ev-content-ul(@pad-right, @pading-left) {
    padding: 0 @pad-right 0 @pading-left;
}
.live-ev-content-ul-li(@height) {
    height: @height;
    border-bottom: 1px solid #CBCBCB;
}
.live-ev-content-ul-li-eventime(@width, @line-height, @font-size, @letter-spacing) {
    width: @width;
    line-height: @line-height;
    font-size: @font-size;
    color: #3B3B3B;
    letter-spacing: @letter-spacing;
    font-weight: 600;
    text-align: left;
}
.live-ev-content-ul-li-event-right(@width, @height) {
    width: @width;
    height: @height;
    vertical-align: middle;
    text-align: right;
}
.live-ev-content-ul-li-event-iten(@width, @height) {
    width: @width;
    height: @height;
    text-align: center;
    vertical-align: bottom;
}
.live-iten-name(@height) {
    width: 37.5%;
    height: @height;
    float: left;
    line-height: @height;
}
.live-iten-team-name(@width, @font-size, @letter-spacing) {
    width: @width;
    font-size: @font-size;
    color: #3B3B3B;
    letter-spacing: @letter-spacing;
    font-weight: bold;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    vertical-align: top;
}
.live-iten-team-score(@font-size, @letter-spacing) {
    font-size: @font-size;
    color: #222222;
    letter-spacing: @letter-spacing;
    font-weight: bold;
    vertical-align: middle;
}
.live-iten-team-logo(@width, @mar-right, @mar-left) {
    width: @width;
    height: @width;
    vertical-align: middle;
    margin-right: @mar-right;
    margin-left: @mar-left;
}
.live-iten-des(@height, @font-size, @letter-spacing) {
    width: 25%;
    height: @height;
    font-size: @font-size;
    color: #969696;
    letter-spacing: @letter-spacing;
    float: left;
}
.live-iten-des-one(@mar-top, @mar-bottom) {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin: @mar-top 0 @mar-bottom 0;
}
.live-iten-des-two(@font-size) {
    font-size: @font-size;
}
.live-iten-empty(@height, @font-size) {
    line-height: @height;
    height: @height;
    font-size: @font-size;
    text-align: center;
}
.live-calendar(@right, @top, @width, @height) {
    position: absolute;
    right: @right;
    top: @top;
    width: @width;
    height: @height;
    background: url(../../assets/img/icon-calendar@3x.png) no-repeat;
    background-size: contain;
    cursor: pointer;
}
@media screen and (max-width: 1640px) {
    .liveEvents{
        position: relative;
        overflow: hidden;
        .snav {
            width: 100%;
            height: 60px;
            top: 60px;
            left: 0;
            z-index: 10;
            position: absolute;
        }
        .liveEventsContent{
            overflow: hidden;
            .live-ev-content(1160px, 150px);
            .eventList{
                .live-ev-list(160px, 700px);
                .title{
                    .live-ev-list-title(50px, 14px, 0.61px);
                }
                .subList{
                    .live-ev-list-sub-list(14px, 0.61px);
                    .subTitle{
                        .live-ev-list-sub-title(40px, 16px, 12px, 0.52px);
                    }
                    ul{
                        li{
                            .live-ev-list-ul-li(40px, 35px);
                            .leftNav{
                                .live-ev-list-ul-li-left-nav(30px, 5px, 10px, 5px, 0);
                            }
                            p {
