                    }
                }
            }
            .eventContent{
                .live-ev-container(980px);
                .dateList{
                    .live-ev-container-date-list(900px, 90px);
                    .date-list{
                        .live-date-list;
                        .swiper-button-prev,.swiper-button-next{
                            .live-button-prev-next(30px, 80px, -42px);
                        }
