<template>
    <div class="sports-model" @click.stop="closeMessage">
        <div class="zhu-window">
            <div class="sure" @click.stop="closeMessage">
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            isHide: null
        }
    },
    props: ['isDisplay'],
    methods: {
        closeMessage() {
            this.isHide = this.isDisplay = false;
            this.$emit('close', this.isHide);
        }
    }
}
</script>

<style lang="less" scoped>
    .sports-model {
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, .4);
        position: fixed;
        left: 0;
        top: 0;
        z-index: 100;
    }
    .zhu-window {
        width: 330px;
        height: 220px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background: url('../../../../assets/sports/world_code.png');
        background-size: 100% 100%;
    }
    .sure {
        width: 98px;
        height: 34px;
        position: absolute;
        top: 91%;
        left: 50%;
        transform: translate(-50%, -50%);
        background: url('../../../../assets/sports/world_sureBtn.png');
        background-size: 100% 100%;
        cursor: pointer;
    }
</style>
    

